<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow p-3 rounded-lg">
            <p class="p-2 text-lg font-semibold"><span class="typcn typcn-pen"></span> LIST ACARA</p>
            <hr>
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
          <div class="table-responsive">
              <table class="table table-sm table-striped table-bordered">
                  <tr>
                      <td colspan="3">
                          <p class="p-2 font-semibold text-lg">Data Acara Tambahan : </p>
                      </td>
                  </tr>
                     <tr>        
                      <td class="capitalize">undangan</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <v-select :options="undangans" label="judul" v-model="vdata.undangan" :reduce="e => e.id"></v-select>
                         
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">judul_acara</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="text" id="judul_acara" name="judul_acara" class="form-control form-control-sm" placeholder="judul_acara" v-model="vdata.judul_acara" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">detail_acara</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form">
                          <textarea type="text" id="detail_acara" name="detail_acara" rows="2" placeholder="detail_acara..." class="form-control md-textarea" v-model="vdata.detail_acara"
                          ></textarea>
                          </div>
                      </td>
                  </tr>
                    <tr>        
                      <td class="capitalize">alamat_acara</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form">
                          <textarea type="text" id="alamat_acara" name="alamat_acara" rows="2" placeholder="alamat_acara..." class="form-control md-textarea" v-model="vdata.alamat_acara"
                          ></textarea>
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">lokasi_acara</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <p class="text-xs text-red-500">Koordinat (22,44)</p>
                          <div class="sm-form ">
                              <input type="text" id="lokasi_acara" name="lokasi_acara" class="form-control form-control-sm" placeholder="lokasi_acara" v-model="vdata.lokasi_acara" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">tanggal_acara</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="date" id="tanggal_acara" name="tanggal_acara" class="form-control form-control-sm" placeholder="tanggal_acara" v-model="vdata.tanggal_acara" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">jam_acara</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="text" id="jam_acara" name="jam_acara" class="form-control form-control-sm" placeholder="jam_acara" v-model="vdata.jam_acara" >
                          </div>
                      </td>
                  </tr>
                   <tr>        
                      <td class="capitalize">gambar_acara</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>
                          <div class="sm-form ">
                              <input type="file" id="filenya" name="gambar_acara" class="form-control form-control-sm" placeholder="gambar_acara" @change="upload">
                              <input type="text" id="gambar_acara" name="gambar_acara" class="form-control form-control-sm" placeholder="gambar_acara" v-model="vdata.gambar_acara" >
                          </div>
                      </td>
                  </tr>
                    <!-- ================================================== -->
              </table>
          </div>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
let urlnya ='https://undangan.officialapp.website'
export default {
layout:'app_admin_undangan',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
      undangans:[]
    };
  },
  methods: {
      upload(){
        let file = document.querySelector("#filenya"); // berikan id pada input file
        sdb.collection('tbuser',false,urlnya).upload(file,true,true) // file,compress,scramble
        .then(res=>{
            console.log(res)
            this.vdata.gambar_acara=res;
            this.$forceUpdate();
        })
      },
    getData(){
      sdb.collection("app_undangan_nikah_acara",false,urlnya).doc().select(`select * from app_undangan_nikah_acara where undangan='${this.$route.query.id}'`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
        sdb.collection("app_undangan_nikah",false,urlnya).doc().select(`select * from app_undangan_nikah`).then(res => {
          this.undangans=res;
          this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_undangan_nikah_acara",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_undangan_nikah_acara",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_undangan_nikah_acara",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
          this.getData();
  },
};
</script>